import React, { Component } from "react";

class PrintTotalCrop extends Component {
  state = {
    imgSrc: "" //     Base64  
  };

  // crop        
  shouldComponentUpdate(nextProps, nextState) {
    return (
      this.props.crops !== nextProps.crops ||
      this.props.showEdit !== nextProps.showEdit
    );
  }

  // canvas crop  ctx.rect  
  getTotalCroppedImg(canvas) {
    return new Promise(resolve => {
      resolve(canvas.toDataURL());
    });
  }

  //       
  async componentDidMount() {
    await this.startWork();
  }

  //        imgSrc  
  async componentDidUpdate(prevProps) {
    if (prevProps !== this.props) {
      await this.startWork();
    }
  }
  // Mount   &&       
  async startWork() {
    const canvas = document.createElement("canvas");
    const { crops, image } = this.props;

    const scaleX = image.naturalWidth / image.width;
    const scaleY = image.naturalHeight / image.height;

    canvas.width = image.width;
    canvas.height = image.height;

    const ctx = canvas.getContext("2d");

    ctx.drawImage(
      image,
      0,
      0,
      image.width * scaleX,
      image.height * scaleY,
      0,
      0,
      image.width,
      image.height
    );

    crops.forEach(function(crop) {
      const x = crop.x;
      const y = crop.y;
      const width = crop.width;
      const height = crop.height;

      ctx.strokeStyle = "yellow";
      ctx.rect(x, y, width, height);

      if (crop.label) {
        ctx.fillStyle = "yellow";
        ctx.fillRect(x, y - 16, crop.label.length * 10, 16);

        ctx.fillStyle = "black";
        ctx.font = "16px serif";
        ctx.fillText(crop.label, x, y);
      }

      ctx.stroke();
    });

    await this.setState(
      {
        imgSrc: await this.getTotalCroppedImg(canvas)
      } /*,
      () => {
        console.log(this.state.imgSrc);
      }*/
    );
  }

  render() {
    return (
      <div>
        <img
          src={this.state.imgSrc}
          alt="crop"
          style={{
            maxWidth: "640px",
            display: this.props.showEdit ? "none" : ""
          }}
          onClick={this.props.onClick}
        />
      </div>
    );
  }
}

export default PrintTotalCrop;
